import LoadingBar from './loading-bar'
import Vue from 'vue'
let timer, t
export default {
	_instance: undefined,
	_init() {
		// new vue
		const instance = new Vue({
			render: h => h(LoadingBar)
		})
		// vuedombody
		document.body.appendChild(instance.$mount().$el) // 
		// vue'loading-bar'
		this._instance = instance.$children[0]
		return this._instance
	},
	_getInstance() {
		// loading-bar
		return this._instance ? this._instance : this._init()
	},
	_clearTimer() {
		clearInterval(timer)
		timer = null
		clearTimeout(t)
		t = null
	},
	_keep({ show = false, width = 0, status = 'primary' }) {
		// 
		this._clearTimer()
		// data
		this._getInstance().show = show
		this._getInstance().width = width
		this._getInstance().status = status
	},
	_hide() {
		t = setTimeout(() => {
			this._clearTimer()
			this._getInstance().show = false
		}, 1000)
	},
	start() {
		let percent = 0
		this._keep({
			show: true,
			width: percent,
			status: 'primary'
		})
		// 
		timer = setInterval(() => {
			// 
			percent += Math.floor(Math.random() * 3 + 1)
			if (percent > 90) {
				this._clearTimer()
			}
			this._getInstance().width = percent + '%'
		}, 200)
	},
	finish() {
		this._keep({
			show: true,
			width: '100%',
			status: 'finish'
		})
		this._hide()
	},
	warning() {
		this._keep({
			show: true,
			width: '100%',
			status: 'warning'
		})
		this._hide()
	},
	error() {
		this._keep({
			show: true,
			width: '100%',
			status: 'error'
		})
		this._hide()
	}
}
