import Notice from '../notice/notice'
import Vue from 'vue'
export default {
	_instance: undefined,
	_init() {
		// new vue
		const instance = new Vue({
			render: h =>
				h(Notice, {
					props: {
						type: 'message'
					}
				})
		})
		// vuedombody
		document.body.appendChild(instance.$mount().$el)
		// vue'notce'
		this._instance = instance.$children[0]
		return this._instance
	},
	_getInstance() {
		// notice
		return this._instance ? this._instance : this._init()
	},
	_addNotice(data) {
		this._getInstance().$set(
			this._getInstance().list,
			new Date().getTime(),
			data
		)
	},
	open(data) {
		data = Object.assign(
			{
				title: '',
				status: 'open'
			},
			data
		)
		this._addNotice(data)
	},
	info(data) {
		data = Object.assign(
			{
				title: '',
				status: 'info'
			},
			data
		)
		this._addNotice(data)
	},
	success(data) {
		data = Object.assign(
			{
				title: '',
				status: 'success'
			},
			data
		)
		this._addNotice(data)
	},
	error(data) {
		data = Object.assign(
			{
				title: '',
				status: 'error'
			},
			data
		)
		this._addNotice(data)
	},
	warning(data) {
		data = Object.assign(
			{
				title: '',
				status: 'warning'
			},
			data
		)
		this._addNotice(data)
	}
}
