<template>
	<article class="container">
		<h1>Modal</h1>
		<section class="section">
			<h2></h2>
			<div>
				<Button @click="handleShow('show1')"></Button>
				<Modal v-model="show1">Modal</Modal>
			</div>
		</section>
		<section class="section">
			<h2></h2>
			<div>
				<Button @click="handleShow('show6')"></Button>
				<Modal v-model="show6" :closable="false" :mask-closable="false"
					>Modal</Modal
				>
			</div>
		</section>
		<section class="section">
			<h2></h2>
			<div>
				<Button @click="handleShow('show2')"></Button>
				<Modal v-model="show2">
					<div slot="header">
						<span style="color: red;"></span>
					</div>
					Modal
					<div slot="footer">
						<a href="#"></a>
					</div>
				</Modal>
			</div>
		</section>
		<section class="section">
			<h2></h2>
			<div>
				<Button @click="handleShow('show3')"></Button>
				<Modal
					v-model="show3"
					title=""
					width="400px"
					confirm-text="ok"
					cancel-text="cancel"
					bottom-align="right"
					>Modal</Modal
				>
			</div>
		</section>
		<section class="section">
			<h2></h2>
			<div>
				<Button @click="handleShow('show4')"></Button>
				<Modal v-model="show4" :centered="true" bottom-align="right"
					>Modal</Modal
				>
			</div>
		</section>
		<section class="section">
			<h2></h2>
			<p class="props">confirmLoadingon-confirm</p>
			<div>
				<Button @click="handleShow('show5')"></Button>
				<Modal
					v-model="show5"
					bottom-align="right"
					:confirmLoading="true"
					@on-confirm="handleConfirmAsync"
					>Modal</Modal
				>
			</div>
		</section>
		<section class="section">
			<h2>loading</h2>
			<div>
				<Button @click="handleShow('show7')"></Button>
				<Modal
					v-model="show7"
					bottom-align="right"
					:contentLoading="contentLoading"
					@on-open="handleOpen"
					>Modal</Modal
				>
			</div>
		</section>
		<section class="section">
			<h2>alert</h2>
			<p class="props">
				alerttypeinfosuccesswarningerrorconfirm
				typeiconNameiconColorrenderrenderbody
				infosuccesswarningerrorrender
			</p>
			<div>
				<Button @click="alert">alert</Button>
				<Button @click="alertInfo">alert</Button>
				<Button @click="info">info</Button>
				<Button @click="success">success</Button>
				<Button @click="warning">warning</Button>
				<Button @click="error">error</Button>
			</div>
		</section>
		<section class="section">
			<h2>confirm</h2>
			<p class="props">
				confirmconfirmrenderrenderbody
			</p>
			<div>
				<Button @click="confirm">confirm</Button>
			</div>
		</section>
	</article>
</template>

<script>
export default {
	data() {
		return {
			show1: false,
			show2: false,
			show3: false,
			show4: false,
			show5: false,
			show6: false,
			show7: false,
			contentLoading: false
		}
	},
	methods: {
		handleShow(key) {
			this[key] = true
		},
		alert() {
			this.$Modal.alert({
				title: '',
				iconName: 'help-circle-outline',
				content: `Modal
                    Modal`,
				bottomAlign: 'center',
				render: h => {
					return h(
						'div',
						{
							style: {
								textAlign: 'center',
								paddingTop: '30px'
							}
						},
						[
							h('Icon', {
								props: {
									iconname: 'lock'
								},
								style: {
									fontSize: '30px',
									color: '#F55'
								}
							}),
							h(
								'p',
								{
									style: {
										lineHeight: '30px',
										marginTop: '15px',
										marginBottom: '20px'
									}
								},
								'render'
							)
						]
					)
				}
			})
		},
		alertInfo() {
			this.$Modal.alert({
				type: 'info',
				title: '',
				iconName: 'help-circle-outline',
				content: `Modal
                    Modal`,
				bottomAlign: 'center',
				confirmText: 'ok'
			})
		},
		info() {
			this.$Modal.info({
				title: '',
				iconName: 'help-circle-outline',
				content: 'ModalModal',
				confirmLoading: true,
				onConfirm: () => {
					this.$Modal.remove()
					this.success()
				}
			})
		},
		success() {
			this.$Modal.success({
				title: '',
				iconName: 'help-circle-outline',
				content: 'ModalModal',
				confirmLoading: true,
				onConfirm: () => {
					setTimeout(() => {
						this.$Modal.remove()
					}, 1000)
				}
			})
		},
		warning() {
			this.$Modal.warning({
				title: '',
				iconName: 'help-circle-outline',
				content: 'ModalModal'
			})
		},
		error() {
			this.$Modal.error({
				title: '',
				iconName: 'help-circle-outline',
				content: 'ModalModal'
			})
		},
		confirm() {
			this.$Modal.confirm({
				title: '',
				iconName: 'help-circle-outline',
				content: 'XXXXXXXXXXXXXXXXX',
				confirmLoading: true,
				onConfirm: () => {
					setTimeout(() => {
						this.$Modal.remove()
					}, 1000)
				}
			})
		},
		handleConfirmAsync() {
			setTimeout(() => {
				this.show5 = false
			}, 3000)
		},
		handleOpen() {
			this.contentLoading = true
			setTimeout(() => {
				this.contentLoading = false
			}, 3000)
		}
	}
}
</script>

