export default {
	props: {
		// 3smalldefault(middle)largeform
		size: {
			type: String,
			validator(value) {
				return ['small', 'default', 'large'].indexOf(value) !== -1
			},
			default() {
				return 'default'
			}
		}
	},
	computed: {
		sizeClasses() {
			if (this.size && this.size !== 'default')
				return this.stylePrefix + '-size-' + this.size
		}
	}
}
