<template>
	<div class="zov-popper">
		<slot></slot>
		<span
			v-show="!this.noArrow"
			class="zov-popper-arrow"
			ref="zov-popper-arrow"
		></span>
	</div>
</template>
<script>
import Popper from 'popper.js'
const prefix = 'zov-popper'
export default {
	name: prefix,
	props: {
		value: {
			type: Boolean,
			default: false
		},
		placement: {
			type: String,
			default: 'bottom'
		},
		fix: {
			type: Boolean,
			default: true
		},
		noArrow: {
			type: Boolean,
			default: false
		},
		size: {
			// 
			type: String,
			default: ''
		}
	},
	data() {
		return {
			popper: null
		}
	},
	watch: {
		size() {
			this.popper.update()
		},
		placement(val) {
			this.popper.options.placement = val
			this.popper.update()
		}
	},
	methods: {
		// csspopperorigin
		resetTransformOrigin(origin) {
			let transObj = {
				right: 'left',
				left: 'right',
				bottom: 'top',
				top: 'bottom'
			}
			this.popper.popper.style.transformOrigin = transObj[origin]
		},
		createPopper() {
			let _this = this
			_this.popper = new Popper(_this.$el.parentNode, _this.$el, {
				placement: _this.placement, // 'bottom'
				positionFixed: _this.fix, // position: fixedfalse
				// eventsEnabled: true, // true
				// removeOnDestroy: false, // destroypopperfalse
				onCreate: data => {
					this.resetTransformOrigin(data.placement)
				},
				onUpdate: data => {
					this.resetTransformOrigin(data.placement)
				},
				// 
				modifiers: {
					arrow: {
						element: this.$refs['zov-popper-arrow']
					},
					computeStyle: {
						gpuAcceleration: false // CSS 3Dpopper
					},
					flip: {
						// behavior: 'clockwise' // 'flip'
					},
					preventOverflow: {
						padding: 5,
						boundariesElement: 'window' //  'window' 'viewport' scrollParent
					}
				}
			})
		}
	},
	mounted() {
		this.createPopper()
	},
	destroyed() {
		this.popper.destroy()
	},
	updated() {
		this.popper.update()
	}
}
</script>
