/**
 * select-headerdate-pickerselecttime-pickercascaderbig-data-list
 **/
export default {
	props: {
		clearable: {
			type: Boolean,
			default: true
		}
	},
	methods: {
		dropShowFocus() {
			this.$refs['zov-select-head'] &&
				this.$refs['zov-select-head'].headFocus()
			this.dropShow = true
		},
		dropHideBlur() {
			this.$refs['zov-select-head'] &&
				this.$refs['zov-select-head'].headBlur()
			this.dropShow = false
		},
		clear() {
			this.query = ''
			this.currentItemArr = []
			this.currentValueArr = []
			this.$emit('input', '')
			this.$emit('on-change', '')
		}
	}
}
