<script>
export default {
	name: 'zov-tab-pane',
	props: {
		// tab-pane
		id: {
			type: String,
			required: true
		},
		// tab
		label: String,
		// tab
		order: {
			type: Number,
			default: 0
		},
		// tab
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		lazy: {
			type: Boolean,
			default: true
		},
		closable: {
			type: Boolean,
			default: false
		}
	},
	mounted() {
		if (this.Tabs === null) {
			throw new Error('TabPaneTabs')
		}
		this.Tabs.addTab(this)
		// 
		if (this.lazy === false) {
			// 
			this.isRender = true
		}
		this.computedLabel = this.$slots.label || this.label
	},
	beforeDestroy() {
		this.Tabs.removeTab(this.id)
	},
	inject: {
		Tabs: {
			default: null
		}
	},
	data() {
		return {
			isRender: false,
			computedLabel: ''
		}
	},
	computed: {
		active() {
			return this.Tabs.activeId === this.id
		},
		show() {
			return this.isRender && this.active
		}
	},
	watch: {
		active(active) {
			if (this.lazy && this.isRender === false && active === true) {
				this.isRender = true
			}
		}
	},
	render() {
		this.computedLabel = this.$slots.label || this.label
		if (this.isRender) {
			return (
				<div
					class="zov-content-item"
					style={{ display: this.show ? 'block' : 'none' }}>
					{this.$slots.default}
				</div>
			)
		}
	}
}
</script>
