<template>
	<div class="zov-slider" :class="classList">
		<div class="zov-slider-bar"></div>
		<div class="zov-slider-tracker" :style="sliderTrackerStyles"></div>
		<div class="zov-slider-marks" v-if="formattedMarks.length">
			<SliderDot
				v-for="mark of formattedMarks"
				:key="'sliderDot-' + mark.value"
				:value="mark.value"
				:label="mark.label"
				:style="mark.style"
				:position="translateValueToPosition(mark.value)"
				:is-active="isActiveMark(mark.value)"
			/>
		</div>
		<SliderHandle
			v-if="range"
			id="begin"
			ref="beginHandle"
			:style="beginHandleStyles"
			:tooltip-text="tipFormatter(beginValue)"
			:tooltip-visible="tooltipVisible"
			:vertical="vertical"
			@increase="increase"
			@reduce="reduce"
		/>
		<SliderHandle
			ref="endHandle"
			id="end"
			:style="endHandleStyles"
			:tooltip-text="tipFormatter(endValue)"
			:tooltip-visible="tooltipVisible"
			:vertical="vertical"
			@increase="increase"
			@reduce="reduce"
		/>
	</div>
</template>

<script>
import SliderHandle from './slider-handle'
import Draggable from '../../utils/draggable'
import { isNumber, isString, isDef, isObject } from '../../utils'
import SliderDot from './slider-dot'

const COMPONENT_NAME = 'zov-slider'

/**
 * value
 * @param value
 * @param compareValues
 * @return {*}
 */
function getClosestValue(value, ...compareValues) {
	let prevDistance = Number.POSITIVE_INFINITY
	let distance = 0
	let resultValue = value
	for (let compareValue of compareValues) {
		if (!isNumber(compareValue)) {
			continue
		}
		distance = Math.abs(value - compareValue)
		if (prevDistance > distance) {
			prevDistance = distance
			resultValue = compareValue
		}
	}
	return resultValue
}

/**
 * mark
 * @param marks marks
 * @param value 
 * @param isForward
 * , valuemarksmarkgroup;
 * isForward = truegroup
 * isForward = falsegroup
 */
function findMarkGroup(marks, value, isForward = true) {
	// 
	let beginIndex = 0
	// 
	let endIndex = marks.length - 1
	if (
		marks.length === 0 || // marks
		value < marks[beginIndex].value || // 
		value > marks[endIndex].value // 
	) {
		return null
	}
	// 
	let midIndex
	// mark
	let midValue
	// mark
	// 1valuemark
	while (endIndex - beginIndex > 1) {
		midIndex = Math.floor((beginIndex + endIndex) / 2)
		midValue = marks[midIndex].value
		// valuemark
		// marks[0, 26, 37], value = 26, [0, 26][26, 37]
		if (value === midValue) {
			// 
			if (isForward) {
				// group = [26, 37]
				beginIndex = midIndex
				endIndex = midIndex + 1
				break
			} else {
				// group = [0, 26]
				beginIndex = midIndex - 1
				endIndex = midIndex
				break
			}
		}
		// 
		if (value > midValue) {
			beginIndex = midIndex
		} else {
			endIndex = midIndex
		}
	}
	return [marks[beginIndex], marks[endIndex]]
}

export default {
	name: COMPONENT_NAME,
	components: {
		SliderHandle,
		SliderDot
	},
	model: {
		prop: 'value',
		event: 'on-change'
	},
	props: {
		// 
		value: {
			type: [Number, Array],
			default: 0,
			validator(value) {
				if (Array.isArray(value)) {
					return value.every(v => typeof v === 'number')
				}
				return true
			}
		},
		// 
		min: {
			type: Number,
			default: 0
		},
		// 
		max: {
			type: Number,
			default: 100
		},
		// tooltip
		tooltipVisible: {
			type: String,
			default: 'default',
			validator(state) {
				return (
					// : handle hover
					state === 'default' ||
					// 
					state === 'hidden' ||
					// 
					state === 'always'
				)
			}
		},
		// tooltip
		tipFormatter: {
			type: Function,
			default: value => value.toString()
		},
		// 
		range: {
			type: Boolean,
			default: false
		},
		// 
		step: {
			type: Number,
			default: 1
		},
		marks: {
			type: Object,
			validator(marks) {
				return Object.keys(marks).every(value => {
					const config = marks[value]
					if (isString(config)) {
						return true
					}
					if (isObject(config)) {
						let result = true
						if (isDef(config.style)) {
							result = result && isObject(config.style)
						}
						if (isDef(config.label)) {
							result = result && isString(config.label)
						}
						return result
					}
					return false
				})
			}
		},
		// marks
		onlyMarks: {
			type: Boolean,
			default: false
		},
		// 
		vertical: {
			type: Boolean,
			default: false
		}
	},
	mounted() {
		this.draggable = new Draggable(this.$el)
			.on(Draggable.Events.DRAG_START, this.onDragstart.bind(this))
			.on(Draggable.Events.DRAGGING, this.onDragging.bind(this))
			.on(Draggable.Events.DRAG_END, this.onDragend.bind(this))
	},
	destroyed() {
		this.draggable.destroyed()
	},
	data() {
		return {
			// 
			beginValue: this.min,
			// 
			endValue: this.min,
			// 
			draggable: null,
			// slider
			sliderRectData: null,
			// , reset
			defaultValue: this.value
		}
	},
	computed: {
		// 
		precision() {
			let value = this.step
			value = value.toString().split('.')
			return value[1] ? value[1].length : 0
		},
		// 
		size() {
			return this.max - this.min
		},
		// 
		beginHandleStyles() {
			return this.translateValueToPosition(this.beginValue)
		},
		// 
		endHandleStyles() {
			return this.translateValueToPosition(this.endValue)
		},
		// 
		sliderTrackerStyles() {
			let {
				beginHandleStyles,
				beginValue,
				endValue,
				size,
				vertical
			} = this
			if (vertical) {
				return {
					bottom: beginHandleStyles.bottom,
					height:
						(((endValue - beginValue) / size) * 100).toFixed(
							this.precision
						) + '%'
				}
			}
			return {
				left: beginHandleStyles.left,
				width:
					(((endValue - beginValue) / size) * 100).toFixed(
						this.precision
					) + '%'
			}
		},
		formattedMarks() {
			let { min, max, marks } = this
			let formattedMarks = []
			if (isDef(marks)) {
				Object.keys(marks).forEach(value => {
					value = Number(value)
					if (isNaN(value) || value < min || value > max) {
						return
					}
					let config = this.marks[value]
					formattedMarks.push({
						value,
						style: config.style,
						label: isString(config) ? config : config.label || value
					})
				})
			}
			formattedMarks.sort((mark1, mark2) => {
				return mark1.value < mark2.value ? -1 : 1
			})
			return formattedMarks
		},
		classList() {
			const classList = []
			if (this.vertical) {
				classList.push(`${COMPONENT_NAME}--vertical`)
			} else {
				classList.push(`${COMPONENT_NAME}--horizontal`)
			}
			return classList
		}
	},
	watch: {
		value: {
			handler(value) {
				this.setValue(value)
			},
			immediate: true
		},
		// step
		step() {
			this.refresh()
		},
		// marks
		marks() {
			this.refresh()
		}
	},
	methods: {
		// 
		normalizeValue(value) {
			let { min, max, step, precision, onlyMarks, marks } = this
			if (!isNumber(value)) {
				return min
			}
			// step
			let nextValue = Math.round(value / step) * step
			// marks
			if (isDef(marks)) {
				// marks
				const closestMarkValue = this.findClosestMarkValue(value)
				// marksmarkValue
				if (onlyMarks) {
					nextValue = closestMarkValue
				} else {
					nextValue = getClosestValue(
						value,
						nextValue,
						closestMarkValue
					)
				}
			}
			nextValue = Number(nextValue.toFixed(precision))
			if (nextValue < min) {
				nextValue = min
			} else if (value > max) {
				nextValue = max
			}
			return nextValue
		},
		// 
		calculateNextValue(value, isForward = true) {
			let { step, onlyMarks, formattedMarks } = this
			// 
			let times = value / step
			let nextValue = value
			// 
			times = isForward ? Math.floor(times) : Math.ceil(times)
			nextValue = times * step + (isForward ? step : -1 * step)
			// marks
			if (formattedMarks.length) {
				// mark
				let minMarkValue = formattedMarks[0].value
				// mark
				let maxMarkValue =
					formattedMarks[formattedMarks.length - 1].value
				// mark
				let computedMarkValue = Number.NaN
				if (onlyMarks) {
					let markGroup = findMarkGroup(
						formattedMarks,
						value,
						isForward
					)
					// mark
					if (markGroup) {
						// mark
						computedMarkValue = isForward
							? markGroup[1].value
							: markGroup[0].value
					}
					nextValue = isNaN(computedMarkValue)
						? nextValue
						: computedMarkValue
				} else {
					if (value > minMarkValue && value < maxMarkValue) {
						// mark
						let markGroup = findMarkGroup(
							formattedMarks,
							value,
							isForward
						)
						// mark
						if (markGroup) {
							// mark
							computedMarkValue = isForward
								? markGroup[1].value
								: markGroup[0].value
						}
					}
					if (isForward && value < minMarkValue) {
						computedMarkValue = minMarkValue
					}
					if (!isForward && value > maxMarkValue) {
						computedMarkValue = maxMarkValue
					}
					nextValue = getClosestValue(
						value,
						nextValue,
						computedMarkValue
					)
				}
			}
			return nextValue
		},
		// markValue
		// TODO dragging
		findClosestMarkValue(value) {
			const marks = this.formattedMarks
			// marks
			if (marks.length === 0) {
				return value
			}
			// mark
			let minMarkValue = marks[0].value
			// mark
			let maxMarkValue = marks[marks.length - 1].value
			if (value <= minMarkValue) {
				return minMarkValue
			}
			// 
			if (value >= maxMarkValue) {
				return maxMarkValue
			}
			const markGroup = findMarkGroup(marks, value)
			return getClosestValue(
				value,
				markGroup[0].value,
				markGroup[1].value
			)
		},
		// slider
		updateSliderRectData() {
			this.sliderRectData = this.$el.getBoundingClientRect()
		},
		// 
		translatePositionToValue({ currentX, currentY }) {
			let { min, size, sliderRectData, vertical } = this
			let percent = 0
			if (vertical) {
				percent =
					(sliderRectData.bottom - currentY) / sliderRectData.height
			} else {
				percent =
					(currentX - sliderRectData.left) / sliderRectData.width
			}
			return min + percent * size
		},
		// 
		translateValueToPosition(value) {
			let { min, size, precision, vertical } = this
			const percent =
				(((value - min) / size) * 100).toFixed(precision) + '%'
			if (vertical) {
				return {
					bottom: percent
				}
			} else {
				return {
					left: percent
				}
			}
		},
		focus() {
			this.$refs.endHandle.focus()
		},
		blur() {
			this.$refs.endHandle.blur()
		},
		/**
		 * 
		 * range,valueNumber
		 * rangevalueNumber[]
		 * @param {Array<Number>|Number} value
		 */
		setValue(value) {
			const { min } = this
			let oldBeginValue = this.beginValue
			let oldEndValue = this.endValue
			let beginValue = min
			let endValue = min
			if (this.range) {
				let normalizeBeginValue = this.normalizeValue(value[0])
				let normalizeEndValue = this.normalizeValue(value[1])
				beginValue = Math.min(normalizeBeginValue, normalizeEndValue)
				endValue = Math.max(normalizeBeginValue, normalizeEndValue)
			} else {
				endValue = this.normalizeValue(value)
			}
			if (beginValue === oldBeginValue && endValue === oldEndValue) {
				return
			}
			if (this.range) {
				value = [beginValue, endValue]
			} else {
				value = endValue
			}
			this.beginValue = beginValue
			this.endValue = endValue
			this.$emit('on-change', value)
		},
		onDragstart(position) {
			this.updateSliderRectData()
			let value = this.translatePositionToValue(position)
			let { beginValue, endValue } = this
			if (this.range) {
				const midValue = (endValue + beginValue) / 2
				if (value <= midValue) {
					beginValue = value
					this.$refs.beginHandle.dragstart()
				} else {
					endValue = value
					this.$refs.endHandle.dragstart()
				}
				value = [beginValue, endValue]
			} else {
				this.$refs.endHandle.dragstart()
			}
			this.setValue(value)
		},
		onDragging(position) {
			let { beginValue, endValue } = this
			let value = this.translatePositionToValue(position)
			if (this.range) {
				// handle
				if (this.$refs.beginHandle.isDragging) {
					// handle
					// handle
					// 
					if (value >= endValue) {
						this.$refs.beginHandle.dragend()
						this.$refs.endHandle.dragstart()
					}
					this.setValue([value, endValue])
					return
				}
				// handle
				if (this.$refs.endHandle.isDragging) {
					// handle
					// handle
					// 
					if (value <= beginValue) {
						this.$refs.endHandle.dragend()
						this.$refs.beginHandle.dragstart()
					}
					this.setValue([beginValue, value])
					return
				}
				return
			}
			this.setValue(value)
		},
		onDragend() {
			if (this.range) {
				this.$refs.beginHandle.dragend()
				this.$refs.endHandle.dragend()
			} else {
				this.$refs.endHandle.dragend()
			}
		},
		refresh() {
			let { beginValue, endValue } = this
			if (this.range) {
				this.setValue([beginValue, endValue])
			} else {
				this.setValue(endValue)
			}
		},
		reset() {
			this.setValue(this.defaultValue)
		},
		isActiveMark(value) {
			const { range, beginValue, endValue } = this
			if (range) {
				return beginValue <= value && value <= endValue
			} else {
				return value <= endValue
			}
		},
		increase(handleId) {
			const { beginValue, endValue, range } = this
			let nextValue
			if (range) {
				if (handleId === 'begin') {
					nextValue = this.calculateNextValue(beginValue)
					nextValue = [nextValue, endValue]
				} else {
					nextValue = this.calculateNextValue(endValue)
					nextValue = [beginValue, nextValue]
				}
			} else {
				nextValue = this.calculateNextValue(endValue)
			}
			this.setValue(nextValue)
		},
		reduce(handleId) {
			const { beginValue, endValue, range } = this
			let nextValue
			if (range) {
				if (handleId === 'begin') {
					nextValue = this.calculateNextValue(beginValue, false)
					nextValue = [nextValue, endValue]
				} else {
					nextValue = this.calculateNextValue(endValue, false)
					nextValue = [beginValue, nextValue]
				}
			} else {
				nextValue = this.calculateNextValue(endValue, false)
			}
			this.setValue(nextValue)
		}
	}
}
</script>
