<template>
	<div class="zov-select">
		<Drop
			zov-internal-reference-select
			:no-arrow="noArrow"
			trigger="focus"
			:never="disabled"
			v-model="dropShow"
		>
			<template #drop-head>
				<SelectHead
					ref="zov-select-head"
					:data="currentItemArr"
					:disabled="disabled"
					:placeholder="placeholder"
					:size="size"
					filterable
					:multiple="multiple"
					:clearable="clearable"
					:label-name="labelName"
					:value-name="valueName"
					v-model="query"
					:dropShow="dropShow"
					@on-remove-tag="select"
					@on-clear="clear"
					@click.native="!disabled && dropShowFocus()"
				/>
			</template>
			<LongList
				class="zov-select-body"
				:style="{
					width: width + 'px'
				}"
				:data="query ? queryResult : currentData"
			>
				<!-- #011 -->
				<template v-slot="{ props }">
					<div
						:class="[
							'zov-select-option',
							{
								'zov-select-option-selected':
									currentValueArr.indexOf(
										props.item[valueName]
									) !== -1
							}
						]"
						:key="props.index + ''"
						@click.stop.capture="select(props.item)"
						:disabled="props.item.disabled"
					>
						<slot :props="props">
							{{ props.item[labelName] || props.item[valueName] }}
						</slot>
						<Icon
							zov-internal-reference-select
							v-if="
								currentValueArr.indexOf(
									props.item[valueName]
								) !== -1
							"
							iconname="checkmark"
						/>
					</div>
				</template>
			</LongList>
			<div
				class="zov-select-no-data"
				v-if="!currentData.length || (!queryResult.length && query)"
			>
				<Spin v-if="queryLoading" />
				<span v-else>{{ noDataText }}</span>
			</div>
			<Spin cover v-if="loading" />
		</Drop>
	</div>
</template>
<script>
import LongList from '../long-list'
import SelectedMixin from '../select/select-mixin'
import worker from '../../worker'
const prefix = 'zov-big-data-list'
export default {
	name: prefix,
	components: {
		LongList
	},
	mixins: [SelectedMixin],
	watch: {
		query(val) {
			this.queryResult = []
			this.queryLoading = true
			if (val === '') return
			this.throttleFn(() => {
				/**
				 * [test: 20256ms]query
				 **/
				// console.time('')
				for (let i = 0; i < this.currentData.length; i++) {
					let item = this.currentData[i]
					if (
						new RegExp(val).test(
							(
								item[this.currentQueryName] ||
								item[this.valueName]
							).toString()
						)
					) {
						this.queryResult.push(item)
					}
				}
				this.queryLoading = false
				// console.timeEnd('')
				/**
				 * [test: 20800ms]queryvue
				 **/
				// console.time('')
				// let _this = this
				// let wk = worker()
				// wk.postMessage({
				//     currentData: _this.currentData,
				//     val: val,
				//     currentQueryName: _this.currentQueryName,
				//     callback: `
				//     let arr = []
				//     for (let i = 0; i < e.data.currentData.length; i++) {
				//         let item = e.data.currentData[i]
				//         if (new RegExp(e.data.val).test(item[e.data.currentQueryName].toString())) {
				//             arr.push(i)
				//         }
				//     }
				//     postMessage(arr)
				// `
				// })
				// wk.onmessage = function (e) {
				//     console.log('query--', e.data.length)
				//     e.data.forEach((item) => {
				//         _this.queryResult.push(_this.currentData[item])
				//     })
				//     console.timeEnd('')
				// }
			}, 500)
		}
	},
	methods: {
		defaultSelected(callback) {
			if (
				this.value === null ||
				this.value === undefined ||
				!this.value.toString()
			) {
				this.loading = false
				return
			}
			this.value instanceof Array
				? (this.currentValueArr = this.value)
				: (this.currentValueArr[0] = this.value)
			/**
			 * 1webworker
			 * webworker
			 * vue
			 **/
			let _this = this
			let wk = worker()
			wk.postMessage({
				currentData: _this.currentData,
				currentValueArr: this.currentValueArr,
				valueName: _this.valueName,
				callback: `
                    let arr = []
                    e.data.currentValueArr.forEach((item) => {
                        for (var i = 0; i < e.data.currentData.length; i++) {
                            if(item === e.data.currentData[i][e.data.valueName]) {
                                arr.push(i)
                                break
                            }
                        }
                    })
                    postMessage(arr)
                `
			})
			wk.onmessage = function(e) {
				// console.log('default--')
				e.data.forEach(item => {
					_this.select(_this.currentData[item], 'default')
				})
				_this.loading = false
				callback && callback()
				wk.terminate()
			}
		}
	}
}
</script>
